@import "../Variables";

.ContactPage {
  min-height: 100vh;
  background: darken($primaryBG, 10%);
  
  .ContactItem {
    position: relative;
    padding: 50px;
    padding-bottom: 0;
    
    .ContactIcon {
      width: 50px;
      height: 50px;
      
      border-radius: 10px;
    }
    
    .ContactMain {
      position: absolute;
      font-size: 1.5rem;
      cursor: pointer;
      
      margin-left: 20px;
      top: 55%;
    }
  }
}

.Mobile .ContactPage {
  .ContactItem {
    .ContactMain {
      a {
        font-size: 1rem;
      }
    }
  }
}