@import "./Variables.scss";

html {
	background-color: $primaryBG;
	color: white;

	-moz-user-select: none;
	-webkit-user-select: none;
	overflow-x: hidden;
}

#root {
	overflow-x: hidden;

	.Spinner {
		@keyframes spinner {
			0% { transform: rotateZ(0deg) }
			100% { transform: rotateZ(360deg) }
		}

		animation-name: spinner;
		animation-duration: 1s;
		animation-timing-function: linear;
		animation-iteration-count: infinite;
	}

	.Dropdown {
		width: 200px;

		position: absolute;
		top: 0; left: -210px; bottom: -1px;
	}

	.Mobile {
		.ClipboardModal {
			height: 55px;
		}
	}

	a {
		color: $primaryColor;

		&:visited {
			color: lighten($primaryColor, 10);
		}
	}
}

.Header {
	position: relative;
	height: 115px;

	background: linear-gradient(to right, $primaryBG -20%, transparent 100%);

	.HeaderMain {
		font-size: 2.5rem;
		font-weight: bolder;

		position: absolute;
		right: 20px;
		top: 5px;
	}
	
	.HeaderButtons {
		margin-left: 70px;
		
		.MainIcon {
			display: inline-block;
			position: absolute;
			left: 20px;
			top: 17px;
			
			width: 70px;
			height: 70px;
			
			background-repeat: no-repeat;
			background-size: 100% 100%;
		}
		
		.HeaderButton {
			cursor: pointer;
			display: inline-block;
			margin: 30px;
			margin-left: 50px;
			padding-bottom: 2px;
			
			font-size: 2rem;
			font-weight: 200;
			
			text-transform: uppercase;
			letter-spacing: 2px;
			
			transition: transform $ft, font-weight $ft, color $ft;
			color: white !important;
			text-decoration: none !important;
			
			&:hover {
				transform: scale(1.1);
			}
			
			&.Active {
				color: lighten($primaryColor, 10%) !important;
				font-weight: bold;
				border-bottom: 2px solid $primaryColor;
			}
		}
	}
}

.Main {
	min-height: 100vh;
}

.Footer {
	background-color: $primaryBG;
	height: 50px;

	position: relative;

	span {
		position: absolute;
		top: 50%; left: 50%;
		transform: translate(-50%, -50%);
	}
}

.Mobile {
	.Header {
		background: $primaryBG;
		height: 100px;
		
		z-index: 1000;
		
		.HeaderButtons {
			:nth-child(2), :nth-child(4), :nth-child(5) { display: none }
		}
	}
	
	.Footer {
		position: fixed;
		left: 0; right: 0; bottom: 0;
		
		span {
			width: 100%;
			left: 0;
			transform: translateY(-50%);
			text-align: center;

			font-size: 0.6rem;
		}
	}
}