@import "../Variables.scss";

.MainPage {
	position: relative;
	min-height: 100vh;

	.Background {
		position: fixed;
		top: 0; left: 0; bottom: 0; right: 0;
		z-index: -5;
		
		width: 100vw;
		height: 100vh;

		background-size: 100% 100%;
		background-repeat: no-repeat;
		
		animation-duration: 5s;
		transition: filter, transform, opacity;
		transition-duration: 2s;

		// filter: blur(5px);

		&.Active
		{
			z-index: -5;

			filter: none;
			transform: none;
			opacity: 1;
		}

		&:not(.Active)
		{
			z-index: -4;

			transform: scale(3);
			opacity: 0;
		}
	}
	
	.MainBody {
		position: absolute;
		top: 20%;
		padding: 50px;
		background: linear-gradient(to right, $primaryBG -20%, transparent 100%);
		
		font-size: 2rem;
		
		b {
			font-size: 3.5rem;
			font-weight: bolder;
		}
		
		.ContentList {
			.ContentItem {
				background: transparent;
				background-size: 100% 100%;
				display: inline-block;
				margin: 20px;
				margin-right: 5px;
				
				width: 50px;
				height: 50px;
			}
			
			.ContentTooltip {
				position: absolute;
				font-size: 1.5rem;
				
				left: 100px;
				bottom: 30px;
				
				b {
					font-size: 2rem;
				}
			}
		}
	}
}

.Mobile .MainPage {
	.MainBody {
		font-size: 1rem;
		padding-bottom: 200px;
		
		b {
			font-size: 2rem;
		}
		
		.ContentList {
			.ContentTooltip {
				position: relative;
				left: 0;
				margin-top: 20px;
				
				font-size: 1rem;
				
				b {
					font-size: 1.5rem;
				}
			}
		}
	}
	
	.Background {
		background-size: auto 100%;
		background-position: center;
	}
}